import { Row, Col } from "antd";
import { withTranslation } from "react-i18next";
import { GameBlock } from "../../common/GameBlock";
import { Fade } from "react-awesome-reveal";

import {
  GamesBlockSection,
  Content,
  ContentWrapper,
  GamesWrapper,
  MinTitle,
  MinPara,
} from "./styles";

interface GamesBlockProps {
  title: string;
  content: string;
  games?: any;
  t: any;
  id: string;
}
const GamesBlock = ({
  title,
  content,
  games,
  t,
  id,
}: GamesBlockProps) => {
  return (
    <GamesBlockSection>
      <Fade direction="left">
        <Row justify="center" align="middle" id={id}>
          <Col lg={11} md={11} sm={11} xs={24}>
            <ContentWrapper>
              <h6>{t(title)}</h6>
              <Content>{t(content)}</Content>
              <GamesWrapper>
                <Row justify="space-between">
                  {typeof games === "object" &&
                    games.map((item: any, id: number) => {
                      return (
                        <Col key={id} span={24}>
                          <GameBlock src={item.image} width="180px" height="180px" link={item.link}/>
                          <MinTitle>{t(item.title)}</MinTitle>
                          <MinPara>{t(item.content)}</MinPara>
                        </Col>
                      );
                    })}
                </Row>
              </GamesWrapper>
            </ContentWrapper>
          </Col>
        </Row>
      </Fade>
    </GamesBlockSection>
  );
};

export default withTranslation()(GamesBlock);
